import React from 'react';
import { storiesOf } from '@storybook/react';

import { TryOn, Hero, Product } from '../components/Image';

export default {
    title: 'Image/Hero',
    component: Image,
    argTypes: {
        backgroundColor: { control: 'color' },
    },
}
const Template = (args) => <Image {...args}/>;

export const Primary = Template.bind({});
// More on args: https://storybook.js.org/docs/react/writing-stories/args
Primary.args = {
  primary: true,
  label: 'Button',
};

const stories = storiesOf ('Image', module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } });

stories.add('Primary', () => {
    return (<Hero/>);
});

stories.add('TryOn', () => {
    return (<TryOn/>);
});

stories.add('Product', () => {
    return (<Product/>);
});




